.todo-list-template {
  background: -webkit-gradient(linear, left top, right top, from(#9CCDAA), to(#FBE0A4));
  background: -webkit-linear-gradient(left, #9CCDAA, #FBE0A4);
  background: -o-linear-gradient(left, #9CCDAA, #FBE0A4);
  background: linear-gradient(to right, #9CCDAA, #FBE0A4);
  width: 512px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); /* 그림자 */ 
  margin: 0 auto; /* 페이지 중앙 정렬 */
  margin-top: 4rem;
}
.title {
  /*padding: 2rem;*/
  font-size: 2.5rem;
  /*text-align: center;*/
  font-weight: 100;
  background: #6CAF8A;
  color: white;
}
.top-bar {
  display: -ms-flexbox;
  display: flex;
  height: 25px;
  font-size: 1rem;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 5px;
  padding-right: 5px;

}
.top-bar-item:nth-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
}
.top-bar-item:nth-child(2) {
  text-align: center;
  -ms-flex: 1 1;
      flex: 1 1;
}
.top-bar-item:nth-child(3) {
  text-align: end;
  -ms-flex: 1 1;
      flex: 1 1;
}
.top-icon {
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}


.form-wrapper {
  padding: 1rem;
  
}
.todos-wrapper {
  min-height: 5rem;
}


